}

.hidden-mobile-submenu {
  height: 0px;
  visibility: hidden;
  opacity: 0;
  transition: all ease-in-out 0.3s;
}

.mobile-main-menu.open .hidden-mobile-submenu {
  height: 100%;
  visibility: visible;
  opacity: 1;
}

.mobile-main-menu.open .open-close:before {
  transform: rotate(0deg);
  transform-origin: center;
}

