.one-item-block {
    margin-bottom: 80px;;
}

.one-item-wrap {
    display: flex;
    justify-content: flex-start;


    .left-part{
        max-width: 700px;
    }
    .right-part{
      max-width: 460px;
      padding-left: 58px;;
    }

    .prices{
      display: flex;;
      margin-bottom: 55px;;
    }
    .oldPrice:after{
      display: none;;
    }

    .color-item{
      width: 36px;
      height:36px;
      margin-right: 16px;
      cursor: pointer;
      position: relative;
      &.active{
        &:before{
          content: "";
          border: dashed;
          width: 50px;
          display: block;
          height: 50px;
          border-radius: 100%;
          background-color: transparent;
          position: absolute;
          left: -8px;
          top: -8px;
          border: 1px dashed #000;
        }
      }
      &:hover{
        &:before{
          content: "";
          border: dashed;
          width: 50px;
          display: block;
          height: 50px;
          border-radius: 100%;
          background-color: transparent;
          position: absolute;
          left: -8px;
          top: -8px;
          border: 1px dashed #000;
        }
      }
    }
 
}

.size-item{
  cursor: pointer;
  height: 36px;
  margin-right: 16px;
  position: relative;
  width: 36px;
  display: flex;
  background-color: #fff;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-size: 11px;
  line-height: 13px;
  display: flex;
  align-items: center;
  text-align: center;
  /* 2 */
  color: #0B0B0B;
  text-align: center;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  &:hover{
    &:before{
      content: "";
      border: dashed;
      width: 50px;
      display: block;
      height: 50px;
      border-radius: 100%;
      background-color: transparent;
      position: absolute;
      left: -8px;
      top: -8px;
      border: 1px dashed #000;
    }
  }
  &.active{
    &:before{
      content: "";
      border: dashed;
      width: 50px;
      display: block;
      height: 50px;
      border-radius: 100%;
      background-color: transparent;
      position: absolute;
      left: -8px;
      top: -8px;
      border: 1px dashed #000;
    }
  }
  &.disable{
    background-color: #ccc;
    cursor: not-allowed;
    &.active{
      &:before{
       display:none;
      }
    }
    &:hover{
      &:before{
        display: none;;
      }
    }
  }
}

