/* Position the image container (needed to position the left and right arrows) */
.container-for-slider {
    position: relative;
    display: flex;
    // max-height: 771px;
    overflow: hidden;
}
  
  /* Hide the images by default */
  .mySlides {
    display: none;
    max-width: 580px;
    overflow: hidden;
    opacity: 0;
    transition: all ease 0.3s;
  }
  .mySlides.active {
    display: block;
    opacity:1
  }
  /* Add a pointer when hovering over the thumbnail images */
  .cursor {
    cursor: pointer;
  }
  .thumbnails{
    max-height:771px;
    overflow-y: scroll;
  }

/* хром, сафари */
.thumbnails::-webkit-scrollbar { width: 0; }

/* ie 10+ */
.thumbnails { -ms-overflow-style: none; }

/* фф (свойство больше не работает, других способов тоже нет)*/
.thumbnails { overflow: -moz-scrollbars-none; }


  /* Next & previous buttons */
  .prev,
  .next {
    cursor: pointer;
    position: absolute;
    top: 40%;
    width: auto;
    padding: 16px;
    margin-top: -50px;
    color: white;
    font-weight: bold;
    font-size: 20px;
    border-radius: 0 3px 3px 0;
    user-select: none;
    -webkit-user-select: none;
  }
  
  /* Position the "next button" to the right */
  .next {
    right: 0;
    border-radius: 3px 0 0 3px;
  }
  
  /* On hover, add a black background color with a little bit see-through */
  .prev:hover,
  .next:hover {
    background-color: rgba(0, 0, 0, 0.8);
  }
  
  /* Number text (1/3 etc) */
  .numbertext {
    color: #f2f2f2;
    font-size: 12px;
    padding: 8px 12px;
    position: absolute;
    top: 0;
  }
  
  /* Container for image text */
  .caption-container {
    text-align: center;
    background-color: #222;
    padding: 2px 16px;
    color: white;
  }
  
  .row:after {
    content: "";
    display: table;
    clear: both;
  }
  
  /* Six columns side by side */
  .column { 
    width: 95px;
    max-height: 126px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 15px; ;

    
    
  }
  
  /* Add a transparency effect for thumnbail images */
  .demo {
    opacity: 0.6;
  }
  
  .active,
  .demo:hover {
    opacity: 1;
  }



